import { UnitNumber } from '../UnitNumber';

//
export function getFsShopDiscountTitle({
    discount_value = 1 || '',
    min_spend = 0,
}) {
    return `Gim ${discount_value} n Ti Thiu ${UnitNumber(min_spend)}`;
}

//
export function getFsCShopDiscountStatus({
    has_chosen_product,
    shop_discount_arr,
    shop_discount_ix,
    best_discount_ix,
    shop_total_price,
}) {
    //
    if (!has_chosen_product) {
        return {
            title: `Shop Voucher gim n (${
                shop_discount_arr[shop_discount_arr.length - 1].discount_value
            })`,
            action: '',
            action_title: 'Xem thm voucher',
        };
    }

    if (shop_discount_ix == -1) {
        if (best_discount_ix >= 0) {
            const shop_discount_obj = shop_discount_arr[best_discount_ix];

            if (shop_discount_obj.status_card == 'available') {
                return {
                    title: `Lu voucher gim gi ${shop_discount_obj.discount_value}`,
                    action: 'save',
                    action_title: 'Lu',
                };
            }
        }

        const shop_discount_obj =
            shop_discount_arr[shop_discount_arr.length - 1];
        const more_spend = shop_discount_obj.min_spend - shop_total_price;

        if (more_spend <= 0) {
            return {
                title: `C th p dng m gim gi ${shop_discount_obj.discount_value}`,
                action: 'can',
                action_title: 'Xem thm voucher',
            };
        }

        return {
            title: `Gim ${shop_discount_obj.discount_value} khi mua thm ${
                shop_discount_obj.min_spend - shop_total_price
            }`,
            action: '',
            action_title: 'Xem thm voucher',
        };
    } else {
        const shop_discount_obj = shop_discount_arr[shop_discount_ix];
        const more_spend = shop_discount_obj.min_spend - shop_total_price;

        if (more_spend <= 0) {
            return {
                title: `Shop Voucher gim ${shop_discount_obj.discount_value}`,
                action: 'applied',
                action_title: 'Xem thm voucher',
            };
        }

        return {
            title: `Cha th p dng voucher gim ${shop_discount_obj.discount_value}`,
            action: '',
            action_title: 'Xem thm voucher',
        };
    }
}
